/* ================================
   Theme: Arcade
   Legible chips/select/accordion; neon grid background kept
   ================================ */

:root.arcade {
  --bg: #0b0f17;
  --surface: #0f1420;
  --surface-contrast: #e8f0ff;

  --text: #e8f0ff;
  --text-muted: #9fb3c8;

  /* High-contrast chip surfaces (opaque for readability) */
  --chip-bg: #162133;
  --chip-text: #e8f0ff;
  --chip-border: rgba(255,255,255,0.12);
  --chip-bg-active: #1e2c45;
  --chip-text-active: #ffffff;

  --accent: #49d6ff;

  --hl-very: rgba(255, 82, 82, 0.28);
  --hl-slight: rgba(255, 187, 66, 0.28);
  --hl-hardword: rgba(78, 156, 255, 0.30);
}

body.arcade {
  background: #0b0f17;
  color: var(--text);
  /* If you have a neon grid bg image, keep it on <body>::before instead of overlaying content */
}
