/* ================================
   Theme: Dog
   NOTE: Dark overlay removed entirely for readability.
   ================================ */

:root.dog {
  --bg: #fff8e6; /* light warm to sit under the dog pattern */
  --surface: rgba(255, 255, 255, 0.98);
  --surface-contrast: #121417;

  --text: #121417;
  --text-muted: #5d626b;

  --chip-bg: rgba(255, 255, 255, 0.98);
  --chip-text: #121417;
  --chip-border: rgba(0,0,0,0.10);
  --chip-bg-active: #f3f7ff;
  --chip-text-active: #0b2a66;

  --accent: #ff7a3d;

  --hl-very: rgba(220, 53, 69, 0.22);
  --hl-slight: rgba(255, 153, 31, 0.25);
  --hl-hardword: rgba(13, 110, 253, 0.20);
}

body.dog {
  background: var(--bg);
  color: var(--text);
  /* Apply your dog pattern to body (e.g., background-image). 
     Do NOT place any translucent overlay above content. */
}
