/* ================================
   Theme: Cat
   Uses same sizes; playful palette with solid surfaces for contrast
   ================================ */

:root.cat {
  --bg: #0d0f12;
  --surface: rgba(18, 22, 28, 0.96);
  --surface-contrast: #f7fbff;

  --text: #f7fbff;
  --text-muted: #aab8c5;

  --chip-bg: rgba(28, 34, 44, 0.96);
  --chip-text: #f7fbff;
  --chip-border: rgba(255,255,255,0.10);
  --chip-bg-active: rgba(38, 46, 60, 0.98);
  --chip-text-active: #ffffff;

  --accent: #f1b14f;

  --hl-very: rgba(255, 72, 88, 0.30);
  --hl-slight: rgba(255, 196, 77, 0.32);
  --hl-hardword: rgba(120, 200, 255, 0.32);
}

body.cat { background: var(--bg); color: var(--text); }
/* If the cat background image exists, apply to body, not over content panels. */
