/* ================================
   Sentence Scratcher — Base Styles
   Shared tokens, layout, and UI components
   ================================ */

/* Root tokens (sizes are theme-agnostic) */
:root {
  --base-fs: 16px;

  /* Radius + paddings */
  --radius: 12px;
  --chip-pad-y: 0.55rem;
  --chip-pad-x: 0.8rem;
  --btn-pad-y: 0.6rem;
  --btn-pad-x: 1rem;

  /* Shadows */
  --shadow-1: 0 1px 2px rgba(0,0,0,0.08), 0 2px 8px rgba(0,0,0,0.06);

  /* Default color tokens (overridden per-theme) */
  --bg: #0b0b0c;
  --surface: #16181c;
  --surface-contrast: #ffffff;
  --text: #e9eef5;
  --text-muted: #adb5bd;
  --chip-bg: #1f232a;
  --chip-text: #e9eef5;
  --chip-border: rgba(255,255,255,0.12);
  --chip-bg-active: #2a2f39;
  --chip-text-active: #ffffff;
  --accent: #3ea6ff;
  --accent-contrast: #0a0a0a;

  /* Highlight colors */
  --hl-very: rgba(255, 68, 68, 0.28);
  --hl-slight: rgba(255, 173, 31, 0.28);
  --hl-hardword: rgba(66, 140, 255, 0.28);

  /* Borders */
  --border: 1px solid var(--chip-border);
}

/* Layout frame */
html, body {
  font-size: var(--base-fs);
  margin: 0;
  padding: 0;
  height: 100%;
  color: var(--text);
  background: var(--bg);
}

.page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
  box-sizing: border-box;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding: 16px 0;
}

.version {
  font-size: 0.9rem;
  color: var(--text-muted);
  /* former "pill" removed on purpose */
}

.toolbar {
  display: flex;
  align-items: center;
  gap: 8px 12px;
  flex-wrap: wrap;
}

/* Panels / surfaces (editor box, accordion body, etc.) */
.surface {
  background: var(--surface);
  color: var(--surface-contrast);
  border-radius: var(--radius);
  border: var(--border);
  box-shadow: var(--shadow-1);
}

/* Stats row chips (Words / Sentences / Grade / Ease) and theme toggles */
.chip {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: var(--chip-pad-y) var(--chip-pad-x);
  border-radius: var(--radius);
  border: var(--border);
  background: var(--chip-bg);
  color: var(--chip-text);
  font-size: 0.95rem;
  line-height: 1;
  user-select: none;
  white-space: nowrap;
}

.chip.is-active {
  background: var(--chip-bg-active);
  color: var(--chip-text-active);
  border-color: transparent;
}

.chip input[type="checkbox"] {
  accent-color: var(--accent);
}

/* Buttons (actions) */
.btn {
  padding: var(--btn-pad-y) var(--btn-pad-x);
  border-radius: var(--radius);
  border: var(--border);
  background: var(--chip-bg);
  color: var(--chip-text);
  font-size: 0.95rem;
  cursor: pointer;
}
.btn:hover { filter: brightness(1.07); }
.btn:active { filter: brightness(0.96); }

/* Select styled as a chip */
.select {
  position: relative;
  display: inline-flex;
  align-items: center;
}
.select select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  padding: calc(var(--chip-pad-y) + 2px) calc(var(--chip-pad-x) + 26px) var(--chip-pad-y) var(--chip-pad-x);
  border-radius: var(--radius);
  border: var(--border);
  background: var(--chip-bg);
  color: var(--chip-text);
  font-size: 0.95rem;
  cursor: pointer;
}
.select::after {
  content: "▾";
  position: absolute;
  right: 10px;
  pointer-events: none;
  font-size: 0.9rem;
  color: var(--chip-text);
}

/* Editor container */
.editor-shell {
  margin-top: 12px;
  padding: 16px;
}
.editor {
  position: relative;
  min-height: 200px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

/* Highlighting */
span.hl-very    { background: var(--hl-very);    border-radius: 4px; box-decoration-break: clone; -webkit-box-decoration-break: clone; }
span.hl-slight  { background: var(--hl-slight);  border-radius: 4px; box-decoration-break: clone; -webkit-box-decoration-break: clone; }
span.hl-hardword{ background: var(--hl-hardword);border-radius: 4px; box-decoration-break: clone; -webkit-box-decoration-break: clone; }

/* Accordion */
.accordion {
  margin-top: 14px;
}
.accordion summary {
  list-style: none;
  cursor: pointer;
}
.accordion summary::-webkit-details-marker { display: none; }
.accordion .summary-chip {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: var(--chip-pad-y) var(--chip-pad-x);
  border-radius: var(--radius);
  border: var(--border);
  background: var(--chip-bg);
  color: var(--chip-text);
  font-size: 0.95rem;
}
.accordion[open] .summary-chip { background: var(--chip-bg-active); }

/* Utility */
.mono { font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, "Liberation Mono", monospace; }
.muted { color: var(--text-muted); }
.grid-gap { display:flex; gap:8px; flex-wrap:wrap; }
