/* ================================
   Theme: Classic
   Only color overrides. Sizing/radius from base.css
   ================================ */

:root.classic {
  --bg: #f7f9fc;
  --surface: #ffffff;
  --surface-contrast: #121417;

  --text: #121417;
  --text-muted: #5f6b7a;

  --chip-bg: #ffffff;
  --chip-text: #121417;
  --chip-border: rgba(0,0,0,0.10);
  --chip-bg-active: #eef3ff;
  --chip-text-active: #0b2a66;

  --accent: #2b6fff;

  --hl-very: rgba(220, 53, 69, 0.25);
  --hl-slight: rgba(255, 193, 7, 0.28);
  --hl-hardword: rgba(13, 110, 253, 0.25);
}

body.classic { background: var(--bg); color: var(--text); }
